var cartQuantities = document.querySelectorAll('.cart-quantity .product-quantity');

if (cartQuantities) {
  cartQuantities.forEach(function (cartQuantity) {
    var quantityInput = cartQuantity.querySelector('input');
    var quantityMinus = cartQuantity.querySelector('.btn-minus');
    var quantityPlus = cartQuantity.querySelector('.btn-plus');

    function updateQuantity(quantity) {
      if (quantity >= 10) {
        quantityInput.value = 10;
      } else if (quantity <= 0) {
        quantityInput.value = 0;
      }

      var itemQuantity = quantityInput.value;
      var itemId = quantityInput.getAttribute('data-id');

      window.location.href = '/cart/change/' + itemId + '?quantity=' + itemQuantity;
    }

    quantityInput.addEventListener('input', function () {
      updateQuantity(quantityInput.value);
    });

    quantityMinus.addEventListener('click', function () {
      var start = parseInt(quantityInput.value);
      var int = parseInt(-1);
      quantityInput.value = start + int;

      updateQuantity(quantityInput.value);
    });

    quantityPlus.addEventListener('click', function () {
      var start = parseInt(quantityInput.value);
      var int = parseInt(1);

      quantityInput.value = start + int;

      updateQuantity(quantityInput.value);
    });
  });
}

var boardClick = document.querySelector('.board-click');
var boardHovers = document.querySelector('.boards-wrap');
var boardHotspots = document.querySelector('.boards-hotspots');

if (boardClick) {
  var allClicks = document.querySelectorAll('.board-click');
  boardHovers.classList.add('loaded');
  setTimeout(function () {
    boardHovers.classList.add('ready');
    document.querySelector('.core-hover').classList.add('active');
    document.querySelector('.core-hotspots').classList.add('active');
  }, 3500);

  allClicks.forEach(function (click) {
    var layer = click.getAttribute('data-layer');
    click.addEventListener('click', function (event) {
      var remove = boardHovers.querySelectorAll('.board-hover');
      var clicked = boardHovers.querySelectorAll('.board-hover[data-layer=' + layer + ']');
      var removeHotspots = boardHotspots.querySelectorAll('.hotspot-container');
      var clickedHotspots = boardHotspots.querySelectorAll('.hotspot-container[data-layer=' + layer + ']');

      remove.forEach(function (item) {
        item.classList.remove('active');
      });
      removeHotspots.forEach(function (item) {
        item.classList.remove('active');
      });

      clicked.forEach(function (item) {
        item.classList.add('active');
      });
      clickedHotspots.forEach(function (item) {
        item.classList.add('active');
      });
    });

    click.addEventListener('mouseenter', function (event) {
      var hovered = boardHovers.querySelectorAll('.board-hover[data-layer=' + layer + ']');
      hovered.forEach(function (item) {
        item.classList.add('hovering');
      });
    });

    click.addEventListener('mouseleave', function (event) {
      var hovered = boardHovers.querySelectorAll('.board-hover[data-layer=' + layer + ']');
      hovered.forEach(function (item) {
        item.classList.remove('hovering');
      });
    });
  });
}

var spectrumHoversWrap = document.querySelector('.spectrum-hovers');

if (spectrumHoversWrap) {
  if (navigator.userAgent.indexOf('Edge') >= 0) {
    document.querySelector('.spectrum-hovers').classList.add('hide-ie');
  }

  var spectrumHovers = document.querySelectorAll('.spectrum-hovers .clip-hover');

  spectrumHovers.forEach(function (hover) {
    var link = hover.getAttribute('data-url');
    hover.addEventListener('click', function () {
      window.location = link;
    });
  });
}

var termsAgree = document.getElementById('termsAgree');
var agreeAddToCart = document.querySelector('.product-add-btn button[name="checkout"]');

function agree() {
  if (termsAgree.checked) {
    agreeAddToCart.disabled = false;
  } else {
    agreeAddToCart.disabled = true;
  }
}

if (termsAgree) {
  termsAgree.addEventListener('change', function () {
    agree();
  });
}

